/* EditOrder.css */
.edit-order-container {
   max-width: 600px;
   margin: 40px auto;
   padding: 20px;
   background-color: #f9f9f9;
   border-radius: 8px;
   box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
 }
 
 .edit-order-form {
   display: flex;
   flex-direction: column;
   gap: 16px; /* Расстояние между группами полей */
 }
 
 .form-group {
   display: flex;
   flex-direction: column;
 }
 
 .form-group label {
   font-weight: 600;
   margin-bottom: 4px;
 }
 
 .attachments-list {
   background-color: #fff;
   border: 1px solid #ccc;
   padding: 12px;
   border-radius: 4px;
 }
 
 .attachments-list ul {
   margin: 0;
   padding: 0;
   list-style-type: none;
 }
 
 .attachment-item {
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin-bottom: 8px;
 }
 
 .delete-attachment-button {
   background-color: #dc3545;
   color: #fff;
   border: none;
   padding: 5px 10px;
   border-radius: 4px;
   cursor: pointer;
 }
 
 .delete-attachment-button:hover {
   background-color: #c82333;
 }
 
 .new-files-container {
   margin-top: 16px;
 }
 
 .edit-order-buttons {
   display: flex;
   gap: 10px;
   margin-top: 20px;
 }
 
 .save-button {
   background-color: #007bff;
   color: #fff;
   border: none;
   padding: 8px 16px;
   border-radius: 4px;
   font-weight: 600;
   cursor: pointer;
 }
 
 .save-button:hover {
   background-color: #0056b3;
 }
 
 .cancel-button {
   background-color: #dc3545;
   color: #fff;
   border: none;
   padding: 8px 16px;
   border-radius: 4px;
   font-weight: 600;
   cursor: pointer;
 }
 
 .cancel-button:hover {
   background-color: #c82333;
 }