/* CreateOrder.css */

.create-order-container {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.create-order-container h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

.create-order-container .message {
  color: #fff;
  background-color: #28a745;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  display: inline-block;
}

.create-order-container .message.error {
  background-color: #dc3545;
}

.create-order-container form {
  display: flex;
  flex-direction: column;
}

.create-order-container input,
.create-order-container textarea {
  width: 100%;
  padding: 10px;
  margin: 8px 0;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
}

.create-order-container input:focus,
.create-order-container textarea:focus {
  border-color: #007bff;
  outline: none;
}

.create-order-container input[type="file"] {
  border: none;
  background: none;
}

.create-order-container button {
  padding: 12px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin-top: 10px;
}

.create-order-container .save-button {
  background-color: #007bff;
  color: white;
  font-weight: bold;
  transition: background 0.3s;
}

.create-order-container .save-button:hover {
  background-color: #0056b3;
}

.create-order-container .back-button {
  background-color: #6c757d;
  color: white;
  font-weight: bold;
  transition: background 0.3s;
}

.create-order-container .back-button:hover {
  background-color: #5a6268;
}

.disabled-form {
  pointer-events: none;
  opacity: 0.6;
  transition: opacity 0.3s ease;
}


/* Адаптация для мобильных устройств */
@media (max-width: 768px) {
  .create-order-container {
    width: 90%;
  }
}