.modern-style {
   max-width: 500px;
   margin: auto;
   padding: 20px;
   background: #f8f9fa;
   border-radius: 12px;
   box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
 }
 
 /* Карточка пользователя */
 .user-info-card {
   background: white;
   padding: 15px;
   border-radius: 8px;
   box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
   margin-bottom: 15px;
 }
 
 .settings-card {
   background: white;
   padding: 12px;
   border-radius: 8px;
   box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
   margin-bottom: 10px;
 }
 
 /* Кнопки */
 .settings-container button {
   width: 100%;
   padding: 10px;
   border: none;
   border-radius: 5px;
   cursor: pointer;
 }
 
 .create-button {
   background: #4CAF50;
   color: white;
 }
 
 .join-button {
   background: #FF9800;
   color: white;
 }
 
 .leave-button {
   background: #FF5252;
   color: white;
 }
 
 .dashboard-button {
   background: black;
   color: white;
 }

 .full-width-input {
   width: 95.5%;
   padding: 10px;
   border: 1px solid #ccc;
   border-radius: 5px;
   margin-bottom: 10px;
   
 }
 