/* Градиентный фон для всей страницы */
.password-reset-container {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 100vh;
   background: linear-gradient(to right, #6a11cb, #2575fc);
   padding: 20px;
 }
 
 /* Карточка с формой */
 .password-reset-card {
   background: white;
   padding: 30px;
   border-radius: 12px;
   box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
   text-align: center;
   width: 100%;
   max-width: 350px;
 }
 
 /* Поля ввода */
 .password-reset-card input {
   width: 100%;
   padding: 12px;
   margin: 10px 0;
   border: 1px solid #ddd;
   border-radius: 8px;
   font-size: 16px;
 }
 
 /* Кнопка (используется тот же класс, что и на странице входа) */
 .auth-button {
   width: 100%;
   padding: 12px;
   background-color: #6a11cb;
   color: white;
   font-size: 16px;
   border: none;
   border-radius: 8px;
   cursor: pointer;
   transition: 0.3s;
 }
 
 .auth-button:hover {
   background-color: #2575fc;
 }
 
 /* Сообщения об ошибках или успехе */
 .error-message {
   color: red;
   font-size: 14px;
   margin-top: 10px;
 }
 
 /* Дополнительное оформление текста */
 .password-reset-card p {
   font-size: 14px;
   margin-bottom: 10px;
 }
 
 /* Адаптивные стили (опционально) */
 @media (max-width: 768px) {
   .password-reset-container {
     padding: 10px;
   }
 
   .password-reset-card {
     width: 90%;
     max-width: 400px;
     padding: 25px;
   }
 
   .password-reset-card input,
   .auth-button {
     font-size: 14px;
     padding: 10px;
   }
 }
 
 @media (max-width: 480px) {
   .password-reset-container {
     padding: 5px;
   }
 
   .password-reset-card {
     width: 100%;
     padding: 20px;
   }
 }
 