/* Пример стилей, аналогичных тем, что используются для страницы входа */
.auth-container {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 100vh;
   background: linear-gradient(to right, #6a11cb, #2575fc);
   padding: 20px;
 }
 
 .auth-card {
   background: white;
   padding: 30px;
   border-radius: 12px;
   box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
   text-align: center;
   width: 100%;
   max-width: 350px;
 }
 
 .auth-card input,
 .auth-card textarea {
   width: 100%;
   padding: 12px;
   margin: 10px 0;
   border: 1px solid #ddd;
   border-radius: 8px;
   font-size: 16px;
 }
 
 .auth-button {
   width: 100%;
   padding: 12px;
   background-color: #6a11cb;
   color: white;
   font-size: 16px;
   border: none;
   border-radius: 8px;
   cursor: pointer;
   transition: 0.3s;
 }
 
 .auth-button:hover {
   background-color: #2575fc;
 }
 
 .error-message {
   color: red;
   font-size: 14px;
 }
 